import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export interface EventRecord {
  eventUid: string
  eventGroupUid?: string | null
  [key: string]: any
}

export interface EventGroupRecord {
  groupUid: string
  [key: string]: any
}

export interface DBStateShape {
  userDB: Record<string, any>
  userEventDB: Record<string, string[]>
  eventDB: Record<string, EventRecord>
  invitedDB: Record<string, any>
  eventGroupDB: Record<string, EventGroupRecord>
  eventInviteDB: Record<string, any>
}

export interface DBState {
  isDBLoaded: boolean
  db: DBStateShape
}

const initialState: DBState = {
  isDBLoaded: false,
  db: {
    userDB: {},
    userEventDB: {},
    eventDB: {},
    invitedDB: {},
    eventGroupDB: {},
    eventInviteDB: {},
  },
}

interface SetDBPayload extends DBStateShape {}

interface AddNewEventToDBPayload {
  userUid: string
  event: EventRecord
  eventGroup: EventGroupRecord | null
}

export const dbSlice = createSlice({
  name: 'db',
  initialState,
  reducers: {
    setDB: (state, action: PayloadAction<SetDBPayload>) => {
      state.db = action.payload
      state.isDBLoaded = true
    },
    addNewEventToDB: (
      state,
      action: PayloadAction<AddNewEventToDBPayload>,
    ) => {
      const { userUid, event, eventGroup } = action.payload

      console.log('Adding Event to DB...')
      console.log(userUid, event, eventGroup)

      // Ajout de l’event
      state.db.eventDB[event.eventUid] = event

      // Initialiser le tableau si besoin
      if (!state.db.userEventDB[userUid]) {
        state.db.userEventDB[userUid] = []
      }
      state.db.userEventDB[userUid].push(event.eventUid)

      // Ajout du groupe si présent
      if (eventGroup !== null && event.eventGroupUid) {
        state.db.eventGroupDB[event.eventGroupUid] = eventGroup
      }
    },
  },
})

export const { setDB, addNewEventToDB } = dbSlice.actions

export default dbSlice.reducer
